.login-button {
  flex: 1;
  background-color: #E15456;
  border-color: #E15456;
  font-weight: bold;
  width: 197px;
  height: 42px;
  font-size: 18px;
  font-weight: 700;
  margin-top: 30px;
  margin-right: 15px;
  border-radius: 5px;
  background: none;
  border: none;
  color: #E15456;
  border-bottom: 1px solid #E15456;
}